          valueOfTerm: "3",
          label: "2019-2020",
        },
        {
          valueOfTerm: "4",
          label: "2019-2020",
        },
      ],
      valueOfTerm: "",
      optionsOfYear: [
        {
          valueOfYear: "2020",
          label: "2020",
        },
        {
          valueOfYear: "2021",
          label: "2021",
        },
      ],
      valueOfYear: "",
      optionsOfClass: [
        {
          valueOfClass: "191",
          label: "191",
        },
        {
          valueOfClass: "202",
          label: "202",
        },
      ],
      valueOfClass: "",
      optionsOfMentalHtlh: [
        {
          statusOfMental: "",
          label: "",
        },
        {
          statusOfMental: "",
          label: "",
        },
      ],
      statusOfMental: "",

      tableData: [],

      currentPage: 1,
      pagesize: 20,

      inputID: "",
      inputName: "",

      optionsOfGrade: [
        {
          valueOfGrade: "1",
          label: "",
        },
        {
          valueOfGrade: "2",
          label: "",
        },
      ],
      valueOfGrade: "",

      optionsOfFeatures: [
        {
          valueOfFeatures: "1",
          label: "",
        },
        {
          valueOfFeatures: "2",
          label: "",
        },
        {
          valueOfFeatures: "3",
          label: "",
        },
        {
          valueOfFeatures: "4",
          label: "",
        },
      ],
      valueOfFeatures: "",
    };
  },
  created() {
    this.tablePrint();
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    // 
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    alignTable() {
      this.$refs.table.doLayout();
    },
    drawLine() {
      // domecharts
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      let dataMax = [
        { name: "", max: "100", color: "rgba(89, 81, 81, 1)" },
        { name: "", max: "100", color: "rgba(89, 81, 81, 1)" },
        { name: "", max: "100", color: "rgba(89, 81, 81, 1)" },
        { name: "", max: "100", color: "rgba(89, 81, 81, 1)" },
      ];
      let option = {
        color: [
          "rgba(49, 218, 7, 1)",
          "rgba(61, 92, 203, 1)",
          "rgba(225, 145, 33, 1)",
        ],
        //
        tooltip: {},
        legend: {
          data: ["2020", "2019", "2018"],
        },
        radar: {
          name: {
            show: true,
            color: "red",
          },
          //   
          indicator: dataMax,
          shape: "circle", //, circle:,polygon:()
        },
        series: [
          {
            type: "radar",
            label: {
              show: true, //
            },
            areaStyle: {}, //
            data: [
              {
                name: "2020",
                value: [80, 90, 80, 82],
                lineStyle: {
                  color: "rgba(80, 81, 80, 1)",
                },
                areaStyle: {
                  color: "rgba(49, 218, 7, 1)",
                },
              },
              {
                name: "2019",
                value: [70, 80, 88, 62],
                areaStyle: {
                  color: "rgba(61, 92, 203, 1)",
                },
                lineStyle: {
                  color: "rgba(80, 81, 80, 1)",
                },
              },
              {
                name: "2018",
                value: [30, 60, 28, 32],
                areaStyle: {
                  color: "rgba(225, 145, 33, 1)",
                },
                lineStyle: {
                  color: "rgba(80, 81, 80, 1)",
                },
              },
            ],
          },
        ],
      };
      // 
      myChart.setOption(option);
    },
    // 
    // that vuethis 
    tablePrint() {
      let that = this;
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/analysis/list",
      })
        .then((response) => {
          console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    edit(row) {
      this.imageForm = row;
    },
    store() {
      let that = this;
      var Time = new Date();
      var updatetime = Time.toLocaleString();
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/analysis/edit",
        params: {
          stuId: that.imageForm.stuId,
          character: that.imageForm.character,
          stuHobby: that.imageForm.stuHobby,
          stuSpecial: that.imageForm.stuSpecial,
          updateTime: updatetime,
        },
      })
        .then(() => {
          that.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    search() {
      let that = this;
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/analysis/search",
        params: {
          schoolDate: that.valueOfYear,
          stuClass: that.valueOfClass,
          stuId: that.inputID,
          stuName: that.inputName,
          stuPsychology: that.statusOfMental,
        },
      })
        .then((response) => {
          console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    calTableData: function() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pagesize,
        this.currentPage * this.pagesize
      );
    },
  },
};
</script>

