  },
  mounted() {},
  methods: {
    // excel
    handleChange(file) {
      this.fileTemp = file.raw;
      let fileName = file.raw.name;
      let fileType = fileName.substring(fileName.lastIndexOf(".") + 1);
      // 
      if (this.fileTemp) {
        if (fileType == "xlsx" || fileType == "xls") {
          this.importf(this.fileTemp);
        } else {
          this.$message({
            type: "warning",
            message: "",
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "",
        });
      }
    },
    handleRemove() {
      this.fileTemp = null;
    },
    //
    importf(obj) {
      this.importdialogVisible = true;
      let _this = this;
      // DOM
      this.file = obj;
      var rABS = false; //
      var f = this.file;
      var reader = new FileReader();
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //
        // var pt = this;
        var wb; //
        var outdata;
        var reader = new FileReader();
        reader.onload = function() {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            // wb = XLSX.read(btoa(fixdata(binary)), { //
            //     type: 'base64'
            // });
            wb = XLSX.read(Buffer.from(binary, "base64").toString("base64"));
          } else {
            wb = XLSX.read(binary, {
              type: "binary",
            });
          }
          // outdata excel
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
          // excel 
          let arr = [];
          outdata.map((v) => {
            let jsonString = JSON.stringify(v)
              .replace(/\//g, "")
              .replace(/\s/gi, "");
            // console.log(jsonString);
            v = JSON.parse(jsonString);
            let obj = {};
            //xxx
            obj.stuId = v[""];
            obj.stuName = v[""];
            obj.schoolDate = v[""];
            obj.stuClass = v[""];
            obj.classTeacher = v[""];
            obj.idCard = v[""];
            obj.updatetime = v[""];
            arr.push(obj);
          });
          _this.accountList = [...arr];
        };
        reader.readAsArrayBuffer(f);
      };
      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },
    importExcel() {
      let that = this;
      this.$axios({
        url: "http://zh.zhihao.kooboo.site/Student/import",
        method: "post",
        data: {
          // importStudent :JSON.stringify(that.accountList)
          importStudent: that.accountList,
        },
      }).then((response) => {
        console.log(response);
        // console.log(that.accountList.length);
      });
      that.importdialogVisible = false;
      that.tablePrint();
    },

    // 
    exportExcel() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(
        document.querySelector("#Student_table")
      );
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          "baseData.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
