    // checkbox
    deleteInfo() {
      // console.log(this.multipleSelection);
      // multipleSelection

      this.$confirm("", "", {})
        .then(() => {
          console.log(this.multipleSelection);
          var ids = this.multipleSelection.map((m) => m.stuId);
          console.log(ids);

          for (let i = 0; i < ids.length; i++) {
            for (let j = 0; j < this.tableData.length; j++) {
              if (this.tableData[j].stuId == ids[i]) {
                this.tableData.splice(j, 1);
              }
            }
            console.log(ids[i]);
            this.$axios({
              method: "POST",
              url: "http://zh.zhihao.kooboo.site/Student/del",
              data: {
                stuId: ids[i],
              },
            })
              .then(() => {})
              .catch((error) => {
                console.log(error);
              });
          }

          this.$message({
            message: "",
            type: "success",
          });
        })
        .catch((e) => {
          console.log(e);
        });
      // this.tablePrint();
    },
    // 
    searchInFo() {
      let that = this;
      this.$axios({
        method: "POST",
        url: "http://zh.zhihao.kooboo.site/Student/list",
        data: {
          schoolDate: that.valueOfYear,
          stuClass: that.valueOfClass,
          stuId: that.inputID,
