          console.log(error);
        });
    },
    // ->dialog->
    newInfo() {
      this.addForm = {};
    },
    putInfo() {
      let that = this;
      var Time = new Date();
      var updatetime = Time.toLocaleString();
      // console.log(updatetime);
      that.tableData.push(that.addForm);
      this.$axios({
        method: "POST",
        url: "http://zh.zhihao.kooboo.site/Student/new",
        data: {
          stuId: that.addForm.stuId,
          stuName: that.addForm.stuName,
