<template>
  <div>
    <div class="headerText">
      <span style="font-weight: bold; color: #777777"></span>
    </div>
    <div class="functionBar" >
      <el-button type="primary" @click="handleadd"></el-button>
      <el-col v-model="filters">
         <el-input
        class="UserManage-inputUserName"
        placeholder=""
        v-model="filters.usrName"
        clearable 
        @clear="searchUser()"
      ></el-input>
      <el-input
        class="UserManage-inputName"
        v-model="filters.name"
        placeholder=""
        clearable 
        @clear="searchUser()"
      ></el-input>
      <el-input
        class="UserManage-inputNum"
        v-model="filters.number"
        placeholder=""
        clearable
        @clear="searchUser()"
      ></el-input>
     
      <el-select v-model="filters.role" class="UserManage-selectCharacter" placeholder="" clearable @clear="searchUser()">
        <el-option
          v-for="item in optionsOfCharacter"
          :key="item.value"
          :label="item.label"
          :value="item.value"></el-option>  
      </el-select>
        
      <el-select v-model="filters.isUse" class="UserManage-selectOpen" placeholder="" clearable @clear="searchUser()">
        <el-option
          v-for="item in optionsOfOpen"
          :key="item.value"
          :label="item.label"
          :value="item.value"></el-option>
      </el-select>

      <el-button type="primary" @click="searchUser"></el-button>

      </el-col> 
    </div>

    <div class="dataTable">
      <!--  -->
      <el-table
        :data="calTableData"
        border
        style="width: 100%"
      >
        <el-table-column prop="usrName" label="" align="center">
        </el-table-column>
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column prop="number" label="" align="center">
        </el-table-column>
        <el-table-column prop="role" label="" align="center">
        </el-table-column>
        <el-table-column prop="updateTime" label="" align="center">
            <template slot-scope="scope">
                <div>{{handleDate(scope.row.updateTime)}}</div>
                <div>{{handleTime(scope.row.updateTime)}}</div>
            </template>
        </el-table-column>
        <el-table-column prop="isUse" label="" align="center">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.isUse"
              active-color="#13ce66"
              inactive-color="#CCCCCC"
               :active-value="1"
              :inactive-value="0"
              @change="changeIsUse(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>

        <el-table-column label="" width="450" align="center">
          <template slot-scope="scope">
            <el-button type="primary" @click="handleUpdate(scope.$index,scope.row)"></el-button>
            <el-button type="danger" @click="deleteuser(scope.$index,scope.row)"></el-button>
            <el-button type="primary"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="tableTag">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[7, 14, 21, 28]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="userlist.length"
      > 
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" :close-on-click-modal="false" ref="editForm">
      <el-form :model="editForm" label-width="150px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="usrName">
          <el-input id="userName"  v-model="editForm.usrName"></el-input>
        </el-form-item>

        <el-form-item label=":" prop="name">
          <el-input id="userRealName" v-model="editForm.name"></el-input>
        </el-form-item>

        <el-form-item label=":" prop="number">
          <el-input id="userNumber" v-model="editForm.number"></el-input>
        </el-form-item>
         
        <el-form-item label=":" prop="role">
          <el-radio-group v-model="editForm.role">
          <el-radio label=""></el-radio>
          <el-radio label="" ></el-radio>
          <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div  style="text-align: right">
        <el-button v-if="dialogStatus=='create'" type="primary" @click="addUser"></el-button>
        <el-button v-else type="primary" @click="updateUser"></el-button>
        <el-button @click="dialogFormVisible=false" type="danger"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import { addUserInfo,
        getAllUser,
        deleteUser,
        updateUser,
        searchUser,
        changeUserIsUse} from "@/network/user_management.js";


// const defaultListQuery = {
//     name: null,
//     pageNum: 1,
//     pageSize: 5,
// }
export default {
  name: "UserManage",
  data() {
    return {
      filters: {
        usrName: '',
        name: '',
        number: '',
        role: '',
        isUse: ''
      },
    //   listQuery: Object.assign({}, defaultListQuery),
    //   listLoading: true,
      currentPage: 1,
      pagesize: 7,
      dialogStatus: '',
      textMap: {
        create:"",
        update:""
      },
      dialogFormVisible: false,
      //
      editForm: {
        usrName: "",
        name: "",
        number: "",
        role:"teacher",
      },
      editFormRules: {
        usrName: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "" , trigger: "blur" }],
        number: [{ required: true, message: "", trigger: "blur" }],
        role: [{ required: true, message: "", trigger: "blur" }],
      },
      userlist: [],
      optionsOfCharacter: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },],
        valueOfCharacter: '',
        optionsOfOpen: [{
          value: '1',
          label: ''
        }, {
          value: '0',
          label: ''
        }, ],
        valueOfOpen: '',
      
    };
  },
  computed: {
    calTableData: function() {
      return this.userlist.slice(
        (this.currentPage - 1) * this.pagesize,
        this.currentPage * this.pagesize
      );
    },
  },
  methods: {
    //(create)
    getUser(){
    //   let para = {pageNum: this.listQuery.pageNum,pageSize:this.listQuery.pageSize}
         getAllUser().then(res =>{
            console.log(res.updateTime);
            this.userlist= [];
            this.userlist = res;
            // console.log(this.userlist);
            
          })
    },
   handleSizeChange: function(size) {
        this.pagesize = size;
        },
    handleCurrentChange: function(currentPage) {
        this.currentPage = currentPage;
    },
    handleDate(time){
		var date = new Date(time);//10*1000131000
		var Y = date.getFullYear() + '-';
		var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
		var D = (date.getDate() < 10 ? '0'+date.getDate() : date.getDate());
		return Y+M+D;
	},
	handleTime(time){
		var date = new Date(time);
		var h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
		var m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
		var s = (date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds());
		return h+m+s;
	},
    //
    searchUser() {
        let para = {usrName: this.filters.usrName,
                        name: this.filters.name,
                        number: this.filters.number,
                        role: this.filters.role,
                        isUse: this.filters.isUse
                        }
            console.log(para);
            searchUser(para).then(res =>{
              console.log(res);
              this.userlist = []
              this.userlist = res;
            //   console.log(this.userlist);
        })
    },

    //
    handleadd() {
      this.dialogStatus = "create",
      this.dialogFormVisible = true,
      this.editForm = {
        usrName: "",
        name: "",
        number: "",
      }
    },

    //
     addUser() {
     this.$refs.editForm.validate(valid =>{
            if(valid){
              this.$confirm("","",{})
              .then(() =>{
                let para = this.editForm;
                console.log(para);
                addUserInfo(para).then(() =>{
                  this.$message({
                    message:"",
                    type:"success"
                  });
                  this.$refs["editForm"].resetFields();
                  this.dialogFormVisible = false;
                  this.getUser();
                });
              })
              .catch(e =>{
                console.log(e);
              });
            }
          });
    },

    //
    handleUpdate(index,row) {
      console.log(index);
      this.dialogStatus = "update",
      this.dialogFormVisible = true,
    //   this.editForm = Object.assign({},row);
    this.editForm = row;
    },

    //
    updateUser() {
       this.$refs.editForm.validate(valid =>{
            if(valid){
              this.$confirm("","",{})
              .then(() =>{
                // let para = Object.assign({},this.editForm);
                let para = this.editForm;
                console.log(para);
                updateUser(para).then(() =>{
                  this.$message({
                    message:"",
                    type:"success"
                  });
                  this.$refs["editForm"].resetFields();
                  this.dialogFormVisible = false;
                  this.getUser();
                });
              })
              .catch(e =>{
                console.log(e);

              });
            }
          });
    },

    //
    deleteuser(index,row) {
      this.$confirm("","",{
            type:"warning"
      })
      .then(() =>{
        let para = {id:row._id};
        deleteUser(para)
        .then(res => {
          console.log(res);
           this.$message({
                message:"",
                type:"success"
              });
              this.getUser();
        });
      })
      .catch(() =>{
      })
    },
    changeIsUse(row){
        console.log(row);
        changeUserIsUse(row)
        .then()
        .catch((err) =>{
            console.log(err);
        })
    },
  },

  mounted() {
    this.getUser();
  }
};
</script>

<style>
.UserManage-inputUserName {
    width: 150px;
}

.UserManage-inputName {
    width: 150px;
}

.UserManage-inputNum {
    width: 150px;
}

.UserManage-selectCharacter {
    width: 150px;

}

.UserManage-selectOpen {
    width: 150px;

}
</style>
