<script>
export default {
  name: "QuestionSurvey",
  data() {
    return {
      FillBlanksData: [
        {
          answerNo: "1",
          answerDetails: "1",
        },
        {
          answerNo: "2",
          answerDetails: "2",
        },
        {
          answerNo: "",
          answerDetails: "",
        },
      ],
      statisticalData: [
        {
          option: "1",
          answerSituation: "50%",
        },
        {
          option: "2",
          answerSituation: "50%",
        },
        {
          option: "3",
          answerSituation: "0%",
        },
        {
          option: "",
          answerSituation: "",
        },
      ],
      generalData: [
        {
          newlyIncreased: "1",
          totalSubmittals: "2",
          averageAnswerTime: "12",
        },
      ],

      statisticsDialogVisible: false,
      checked: true,
      activeName: "first",
      addDialogVisible: false,
      addInformationForm: {
        questionTitle: "",
        questionType: "",
        sendObject: "",
        questionExplainz: "",
        submQuantity: "",
      },

      addInformationFormRules: {
        questionTitle: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        questionType: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        sendObject: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
      },
      basicDialogVisible: false,
      basicInformationForm: {
        questionTitle: "",
        questionType: "",
        sendObject: "",
        questionExplainz: "",
      },
      basicInformationFormRules: {
        questionTitle: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        questionType: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        sendObject: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
      },

      optionsOfMore: [
        {
          valueOfMore: "1",
          label: "",
        },
        {
          valueOfMore: "2",
          label: "",
        },
        {
          valueOfMore: "3",
          label: "",
        },
        {
          valueOfMore: "4",
          label: "",
        },
        {
          valueOfMore: "5",
          label: "",
        },
      ],
      valueOfMore: "",
      inputTitle: "",
      optionsOfType: [
        {
          valueOfType: "",
          label: "",
        },
        {
          valueOfType: "",
          label: "",
        },
        {
          valueOfType: "",
          label: "",
        },
      ],
      valueOfType: "",
      optionsOfObject: [
        {
          valueOfObject: "",
          label: "",
        },
        {
          valueOfObject: "",
          label: "",
        },
        {
          valueOfObject: "",
          label: "",
        },
      ],
      valueOfObject: "",
      currentPage: 1,
      pagesize: 20,
      tableData: [],
    };
  },
  computed: {
    calTableData: function() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pagesize,
        this.currentPage * this.pagesize
      );
    },
  },
  created() {
    this.tablePrint();
  },
  mounted() {},
  methods: {
    // 
    putInfo() {
      let that = this;
      var Time = new Date();
      var updateTime = Time.toLocaleString();
      console.log(updateTime);
      let index = that.tableData.length + 1; // 
      // todo pushtableData
      this.$axios({
        method: "POST",
        url: "http://zh.zhihao.kooboo.site/question/add",
        data: {
          questionNo: index,
          questionTitle: that.addInformationForm.questionTitle,
          questionType: that.addInformationForm.questionType,
          sendObject: that.addInformationForm.sendObject,
          questionExplainz: that.addInformationForm.questionExplainz,
          updateTime: updateTime,
        },
      })
        .then(() => {
          that.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
      that.addInformationForm = {};
    },
    // 
    storeInfo() {
      let that = this;
      var Time = new Date();
      var updateTime = Time.toLocaleString();
      console.log(updateTime);
      // console.log(that.editForm.activityType);
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/question/edit",
        params: {
          questionNo: that.basicInformationForm.questionNo,
          questionTitle: that.basicInformationForm.questionTitle,
          questionType: that.basicInformationForm.questionType,
          sendObject: that.basicInformationForm.sendObject,
          questionExplainz: that.basicInformationForm.questionExplainz,
          updateTime: updateTime,
        },
      })
        .then(() => {
          this.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    choiceQuestionArraySpanMethod({ rowIndex, columnIndex }) {
      if (rowIndex === this.statisticalData.length - 1) {
        if (columnIndex === 0) {
          return [1, 2];
        } else if (columnIndex === 1) {
          return [0, 0];
        }
      }
    },

    completionArraySpanMethod({ rowIndex, columnIndex }) {
      if (rowIndex === this.FillBlanksData.length - 1) {
        if (columnIndex === 0) {
          return [1, 2];
        } else if (columnIndex === 1) {
          return [0, 0];
        }
      }
    },

    handleClick(tab, event) {
      console.log(tab, event);
    },
    //  switch
    // isUse(row)

    isUse(row) {
      console.log(row);
      // console.log(that.editForm.activityType);
      var Time = new Date();
      var updateTime = Time.toLocaleString();
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/question/publish",
        params: {
          questionNo: row.questionNo,
          isPublish: row.isPublish,
          updateTime: updateTime,
        },
      })
        .then(() => {
          this.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 
    // that vuethis 
    tablePrint() {
      let that = this;
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/question/list",
      })
        .then((response) => {
          //console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    searchInfo() {
      let that = this;
      // console.log(that.inputTitle);
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/question/search",
        params: {
          questionTitle: that.inputTitle,
          questionType: that.valueOfType,
          sendObject: that.valueOfObject,
        },
      })
        .then((response) => {
          // console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //   
    editInFo(row) {
      // rowdataTable
      let that = this;
      // console.log(row);
      that.basicInformationForm = "";
      that.basicInformationForm = row;
      //that.valueOfActType = that.editForm.activityType;
    },
  },
};
</script>

