<template>
  <div>
    <div class="headerText">
      <span style="font-weight: bold; color: #777777"></span>
    </div>

    <div class="functionBar">
      <div class="DailyActivity-buttonContainer">
        <el-button type="primary" @click="addDialogVisible = true; newInfo()"
          ></el-button
        >
      </div>

      <div class="DailyActivity-selectContainer">
        <el-input
          v-model="inputActivity"
          placeholder=""
          class="DailyActivity-inputActivity"
          clearable
          @clear="tablePrint()"
        ></el-input>
        <el-button
          type="primary"
          class="DailyActivity-searchButton"
          @click="searchInfo()"
          ></el-button
        >
      </div>
    </div>

    <div class="dataTable" style="width: 100%">
      <el-table :data="calTableData" border>
        <el-table-column prop="activityName" label="" align="center">
        </el-table-column>
        <el-table-column prop="activityType" label="" align="center">
        </el-table-column>
        <el-table-column prop="activitySites" label="" align="center">
        </el-table-column>
        <el-table-column prop="activityPhoto" label="" align="center">
          <img src="../../assets/Pics/activity.png" alt="" />
        </el-table-column>
        <el-table-column
          prop="activityAbsentenceNum"
          label="/"
          align="center"
        >
        </el-table-column>
        <el-table-column prop="updateTime" label="" align="center">
        </el-table-column>
        <el-table-column
          prop="operation"
          label=""
          align="center"
          class="DailyActivity-tableButton"
          width="280px"
        >
          <template slot-scope="scope">
            <el-button
              type="primary"
              @click="
                editDialogVisible = true;
                editInFo(scope.row);
              "
              ></el-button
            >
            <el-button type="primary"></el-button>
            <el-button type="danger" @click="deleteInfo(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="tableTag">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length"
      >
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%">
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="activityName">
          <el-input
            v-model="addForm.activityName"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="activityType">
          <el-select v-model="addForm.activityType" placeholder="">
            <el-option
              v-for="item in optionsOfActType"
              :key="item.valueOfActType"
              :label="item.label"
              :value="item.valueOfActType"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="activitySites">
          <el-input
            v-model="addForm.activitySites"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="undertaker">
          <el-input
            v-model="addForm.undertaker"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="PersonInCharge">
          <el-input
            v-model="addForm.PersonInCharge"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <el-date-picker
              v-model="valueOfActTime"
              type="datetimerange"
              :picker-options="pickerOptions"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              align="right"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item label="" prop="activityIntroduction">
          <el-input
            type="textarea"
            v-model="addForm.activityIntroduction"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          @click="
            addDialogVisible = false;
            putInfo();
          "
          > </el-button
        >
        <el-button @click="addDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="activityName">
          <el-input
            v-model="editForm.activityName"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="activityType">
          <el-select v-model="editForm.activityType" placeholder="">
            <el-option
              v-for="item in optionsOfActType"
              :key="item.valueOfActType"
              :label="item.label"
              :value="item.valueOfActType"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="activitySites">
          <el-input
            v-model="editForm.activitySites"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="undertaker">
          <el-input
            v-model="editForm.undertaker"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="PersonInCharge">
          <el-input
            v-model="editForm.PersonInCharge"
            placeholder=""
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <el-date-picker
              v-model="valueOfActTime"
              type="datetimerange"
              :picker-options="pickerOptions"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              align="right"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item label="" prop="activityIntroduction">
          <el-input
            type="textarea"
            v-model="editForm.activityIntroduction"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="activityPhoto">
          <el-button size="small" type="primary"></el-button>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          @click="
            editDialogVisible = false;
            storeInfo();
          "
          > </el-button
        >
        <el-button @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "DailyActivity",
  data() {
    return {
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        actName: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        actType: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        actAddress: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },

      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      valueOfActTime: "",

      optionsOfActType: [
        {
          valueOfActType: "",
          label: "",
        },
        {
          valueOfActType: "",
          label: "",
        },
      ],
      valueOfActType: "",
      addDialogVisible: false,
      addForm: {},
      addFormRules: {
        actName: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        actType: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        actAddress: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },

      inputActivity: "",
      tableData: [],
      currentPage: 1,
      pagesize: 20,
    };
  },
  created() {
    this.tablePrint();
  },
  mounted() {},
  methods: {
    // 
    handleSizeChange: function(size) {
      this.pagesize = size;
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
    },
    // 
    // that vuethis 
    tablePrint() {
      let that = this;
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/daily/list",
      })
        .then((response) => {
          // console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // ->dialog->
    newInfo() {
      this.addForm = {};
    },
    // 
    putInfo() {
      let that = this;
      let index = that.tableData.length + 1;
      var Time = new Date();
      var updateTime = Time.toLocaleString();
      console.log(updateTime);
      // console.log(that.addForm);
      this.$axios({
        method: "POST",
        url: "http://zh.zhihao.kooboo.site/daily/add",
        data: {
          activtyNo: index,
          activityName: that.addForm.activityName,
          activityType: that.addForm.activityType,
          activitySites: that.addForm.activitySites,
          undertaker: that.addForm.undertaker,
          PersonInCharge: that.addForm.PersonInCharge,
          activityStartTime: that.valueOfActTime[0].toLocaleString(),
          activityEndTime: that.valueOfActTime[1].toLocaleString(),
          activityIntroduction: that.addForm.activityIntroduction,
          updateTime: updateTime,
        },
      })
        .then(() => {
          that.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
      that.addForm = {};
    },
    //   
    editInFo(row) {
      // rowdataTable
      let that = this;
      // console.log(row);
      that.editForm = "";
      that.editForm = row;
      that.valueOfActType = that.editForm.activityType;
    },
    // 
    storeInfo() {
      let that = this;
      console.log(that.editForm);
      console.log(that.editForm.activityType);
      var Time = new Date();
      var updateTime = Time.toLocaleString();
      console.log(updateTime);
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/daily/edit",
        params: {
          activtyNo: that.editForm.activtyNo,
          activityName: that.editForm.activityName,
          activityType: that.editForm.activityType,
          activitySites: that.editForm.activitySites,
          undertaker: that.editForm.undertaker,
          PersonInCharge: that.editForm.PersonInCharge,
          activityStartTime: that.editForm.activityStartTime,
          activityEndTime: that.editForm.activityEndTime,
          activityIntroduction: that.editForm.activityIntroduction,
          updateTime: updateTime,
        },
      })
        .then(() => {
          this.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
        
    },
    // 
    deleteInfo(row) {
      // console.log(row);
      console.log(row);
      // var deleteID = row._id;
      
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/daily/delete",
        params: {
          activtyNo: row.activtyNo,
        },
      })
        .then(() => {
          // console.log(response);
          this.tablePrint();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    // 
    searchInfo() {
      let that = this;
      this.$axios({
        method: "GET",
        url: "http://zh.zhihao.kooboo.site/daily/search",
        params: {
          activityName: that.inputActivity,
        },
      })
        .then((response) => {
          // console.log(response);
          that.tableData = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    calTableData: function() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pagesize,
        this.currentPage * this.pagesize
      );
    },
  },
};
</script>

<style>
.DailyActivity-buttonContainer {
  flex: 3;
}
.DailyActivity-selectContainer {
  flex: 7;
  display: flex;
  justify-content: flex-end;
}
.DailyActivity-inputActivity {
  width: 150px;
  margin-right: 2%;
}
