<template>
  <div>
    <div class="headerText">
      <span style="font-weight: bold; color: #777777"></span>
    </div>

    <div class="functionBar">
      <el-button type="primary" @click="handleAdd"></el-button>
      <div class="CharacterManage-selectCotainer">
        <el-row v-model="filters" style="flex">
          <el-input
            class="CharacterManage-inputcharacterName"
            v-model="filters.name"
            placeholder=""
            clearable
            @clear="searchRole()"
          ></el-input>
          <el-select
            class="CharacterManage-selectEnabled"
            v-model="filters.isUse"
            placeholder=""
            clearable
            @clear="searchRole()"
          >
            <el-option
              v-for="item in optionsOfEnabled"
              :key="item.valueOfEnabled"
              :label="item.label"
              :value="item.valueOfEnabled"
            >
            </el-option>
          </el-select>
          <el-button type="primary" @click="searchRole()"></el-button>
        </el-row>
      </div>
    </div>

    <div class="dataTable">
      <el-table :data="calTableData" border style="width: 100%"  @selection-change="handleSelectionChange">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column prop="isBuitIn" label="" align="center">
        </el-table-column>
        <el-table-column prop="describe" label="" align="center">
        </el-table-column>
        <el-table-column prop="updateTime" label="" align="center">
             <template slot-scope="scope">
               <div>{{handleDate(scope.row.updateTime)}}</div>
				<div>{{handleTime(scope.row.updateTime)}}</div>
            </template>
        </el-table-column>
        <el-table-column prop="isUse" label="" align="center">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.isUse"
              active-color="#13ce66"
              inactive-color="#CCCCCC"
              :active-value="1"
              :inactive-value="0"
              @change="changeIsUse($event,scope.$index,scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              type="primary"
              v-if="scope.row.isBuitIn == '0'"
              @click="handleUpdate(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              type="danger"
              v-if="scope.row.isBuitIn == '0'"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="tableTag">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[7, 14, 21, 28]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableList.length"
      >
      </el-pagination>
    </div>

    <!-- dialog -->
    <!-- todo -->
    <el-dialog
      :title="textMap[dialogStatus]"
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addForm"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" label=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="describe">
          <el-input type="textarea" v-model="addForm.describe"
            ></el-input
          >
        </el-form-item>
        <el-form-item label="" prop="jurisdiction">
          <div class="block">
            <span class="jurisdiction">
            <el-cascader
              :options="options"
              @change="handleChange"
            ></el-cascader>
            </span>
          </div>
        </el-form-item>
        <el-form-item label="" prop="power">
          <el-tree
            :data="data"
            show-checkbox
            node-key="id"
            :default-expanded-keys="[1]"
            :default-checked-keys="[1, 2, 3, 4, 5, 6, 7]"
            :props="defaultProps"
          >
          </el-tree>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button v-if="dialogStatus == 'create'" type="primary" @click="add"> </el-button>
        <el-button v-else type="primary" @click="update"></el-button>
        <el-button @click="addDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>


<script>
import {
  addRoleInfo,
  updateRoleInfo,
  getAllRoleInfo,
  searchRoleInfo,
  deleteRoleInfo,
  changeIsUse,
} from '@/network/Role_management.js'

// import Moment from 'moment'

// const defaultListQuery = {
//   name: null,
//   pageNum: 1,
//   pageSize: 5,
// }
export default {
  name: 'CharacterManage',
  data() {
    return {
      filters: {
        name: '',
        isUse: '',
      },
      data: [
        {
          id: 1,
          label: '',
          children: [
            {
              id: 3,
              label: '',
            },
            {
              id: 4,
              label: '',
            },
            {
              id: 5,
              label: '',
            },
            {
              id: 6,
              label: '',
            },
            {
              id: 7,
              label: '',
            },
          ],
        },
        {
          id: 2,
          label: '',
        },
      ],
      defaultProps: {
        children: 'children',
        label: 'label',
      },
      value: [],
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
          children: [
            {
              value: '2018',
              label: '2018',
            },
            {
              value: '2019',
              label: '2019',
            },
            {
              value: '2020',
              label: '2020',
            },
            {
              value: '2021',
              label: '2021',
            },
          ],
        },
        {
          value: '',
          label: '',
          children: [
            {
              value: '181',
              label: '181',
            },
            {
              value: '182',
              label: '182',
            },
            {
              value: '191',
              label: '191',
            },
            {
              value: '192',
              label: '192',
            },
          ],
        },
      ],
      inputcharacterName: '',
      optionsOfEnabled: [
        {
          valueOfEnabled: '1',
          label: '',
        },
        {
          valueOfEnabled: '0',
          label: '',
        },
      ],
      valueOfEnabled: '',
    //   listQuery: Object.assign({}, defaultListQuery),
      currentPage: 1,
      pagesize: 7,
    //   total: 1,
      tableList: [],
      textMap: {
        create: '',
        update: '',
      },
      dialogStatus: '',
      addDialogVisible: false,
      addForm: {
        name: '',
        describe: '',
        jurisdiction: '',
        power: '',
      },
      addFormRules: {
          name: [{ required: true, message: "" , trigger: "blur" }],
          describe: [{ required: true, message: "", trigger: "blur" }],
        //   jurisdiction: [{ required: true, message: "", trigger: "blur" }],
        //   power: [{ required: true, message: "", trigger: "blur" }],
      },
    }
  },
  computed: {
    calTableData: function () {
        return this.tableList.slice(
            (this.currentPage - 1) * this.pagesize,
            this.currentPage * this.pagesize
        );
    },
  },
  methods: {
    handleSizeChange: function(size) {
        this.pagesize = size;
        },
    handleCurrentChange: function(currentPage) {
        this.currentPage = currentPage;
    },
    
    // handleDate(time) {
    //     return time.format("YYYY-MM-DD HH:mm:ss")
    // },
    handleDate(time){
		var date = new Date(time);//10*1000131000
		var Y = date.getFullYear() + '-';
		var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
		var D = (date.getDate() < 10 ? '0'+date.getDate() : date.getDate());
		return Y+M+D;
	},
	handleTime(time){
		var date = new Date(time);//10*1000131000
		var h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
		var m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
		var s = (date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds());
		return h+m+s;
	},
    getAllRole() {
    //   let para = {
    //     pageNum: this.listQuery.pageNum,
    //     pageSize: this.listQuery.pageSize,
    //   }
    getAllRoleInfo().then((res) => {
        this.tableList = []
        this.tableList = res
      })
    },
    searchRole() {
       let para = {
          name: this.filters.name,
          isUse: this.filters.isUse,
        }
        searchRoleInfo(para).then((res) => {
          console.log(res)
          this.tableList = []
          this.tableList = res
        })
    },
    handleAdd() {
      this.addDialogVisible = true
      this.dialogStatus = 'create'
      this.addForm = {
        name: '',
        describe: '',
        jurisdiction: '',
        power: '',
      }
    },
    add() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {})
            .then(() => {
            //   let para = Object.assign({}, this.addForm)
            let para = this.addForm;
              console.log(para)
              addRoleInfo(para).then(() => {
                this.$message({
                  message: '',
                  type: 'success',
                })
                this.$refs['addForm'].resetFields()
                this.addDialogVisible = false
                this.getAllRole()
              })
            })
            .catch((e) => {
              console.log(e)
            })
        }
      })
    },
    handleUpdate(index, row) {
      this.addDialogVisible = true
      this.dialogStatus = 'update'
    //   this.addForm = Object.assign({}, row)
    this.addForm = row;
      // this.addForm = {
      //   name: row.name,
      //   describe: row.describe,
      //   // jurisdiction:"",
      //   // menuPermissions: ""
      // }
    },
    update() {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {}).then(() => {
            // let para = Object.assign({}, this.addForm)
            let para = this.addForm;
            console.log(para)
            updateRoleInfo(para).then(() => {
              this.$message({
                message: '',
                type: 'success',
              })
              this.$refs['addForm'].resetFields()
              this.addDialogVisible = false
              this.getAllRole()
            })
          })
        }
      })
    },
    handleDelete(index, row) {
      this.$confirm('', '', {
        type: 'warning',
      }).then(() => {
        let para = { id: row._id }
        deleteRoleInfo(para)
          .then(() => {
            this.$message({
              message: '',
              type: 'success',
            })
            this.getAllRole()
          })
          .catch((err) => {
            console.log(err)
          })
      })
    },
    changeIsUse(data,index,row) {
    // console.log(row);
      changeIsUse(row)
      .then()
      .catch((err) => {console.log(err);})
    }
  },
  mounted() {
    this.getAllRole()
  },
}
</script>

<style>
.CharacterManage-selectCotainer {
  flex: 8;
  display: flex;
  justify-content: flex-end;
}

.CharacterManage-selectEnabled {
  margin-right: 2%;
  width: 150px;
}

.CharacterManage-inputcharacterName {
  flex: 3;
  max-width: 30%;
  margin-right: 2%;
}
</style>
